*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    font-family: 'Raleway', sans-serif;
}

.error,.info {
    text-align: center;
    margin: 0 auto;
    color: #535353;
    opacity: 0.6;
    text-shadow: 0 0 2px #868686;
}

header {
    padding: 30px;
    background-color: #f1f1f1;
    -webkit-box-shadow: 0 5px 5px #dedede;
            box-shadow: 0 5px 5px #dedede;
}

.logo {
    color: #ff9d00;
    font-weight: bold;
    font-size: 50px;
    font-family: 'Anton', sans-serif;
}

@media (max-width: 400px) {
    header {
        padding: 20px;
    }

    .logo {
        font-size: 30px;
    }
}
.infinite-scroll-component {
    display: -ms-flexbox;
    display: flex;
    padding: 60px;
    padding-bottom: 120px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    position: relative;
}

.infinite-scroll-component li {
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin-bottom: 50px;
    list-style: none;
}

.loadInfo {
    position: absolute;
    bottom: 100px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

@media (max-width: 400px) {
    .MovieList {
        padding: 20px;
        padding-bottom: 100px;
    }
}
.MovieCard {
    width: 250px;
    height: 350px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 40px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    cursor: pointer;
    position: relative;
    -webkit-box-shadow: 0px 0px 5px #acacac;
            box-shadow: 0px 0px 5px #acacac;
    margin: 0 auto;
}

.MovieCard:hover {
    -webkit-box-shadow: 0px 0px 15px #868686;
            box-shadow: 0px 0px 15px #868686;
}

.movieRating {
    position: absolute;
    background-color: whitesmoke;
    border-radius: 10px;
    text-align: center;
    padding: 15px;
    right: 20px;
    bottom: 25%;
    font-weight: bold;
    /*box-shadow: 1px 1px 1px #aaaaaa;*/
}

.cardFooter {
    position: absolute;
    bottom: 0;
    height: 20%;
    width: 100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 40px;
    padding: 15px;
    padding-right: 20px;
    background-color: rgba(126, 126, 126, 0.7);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    color: whitesmoke;
    font-weight: bold;
    z-index: 2;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.movieTitle {
    font-size: 14px;
}

.cardFooter:hover {
    height: 100%;
    background-color: rgba(126, 126, 126, 0.9);
    border-top-left-radius: 40px;
    border-top-right-radius: 10px;
    padding: 20px;
}
.cardFooter:hover>.movieDescription {
    height: auto;
}

.movieDescription {
    max-height: 70%;
    overflow-y: hidden;
    height: 0;
    font-size: 11px;
    font-weight: normal;
}



